import { Logger, LogLevel } from "./logger";

export class catcher {
    static tryLog({ cb, catchLog }: { cb: () => void, catchLog?: (err: any) => string }) {
        try {
            cb();
        } catch (error) {
            const message = catchLog ? catchLog(error) : "catcher.tryLog error happened";
            Logger.log({ message, error, level: LogLevel.error });
        }
    }
}


