import { IBulkManager } from "./bulkManager";
import { WindowHelper } from "./windowHelper";

export type TimeFormat = "date" | "time" | "datetime"| "datepath";

export enum LogLevel {
    debug = "DEBUG",
    info= "INFO",
    log = "LOG",
    trace = "TRACE",
    warn = "WARNING",
    error = "ERROR"
}


export const LogLevelLookup = {
    DEBUG: 10,
    INFO:20,
    LOG: 30,
    TRACE: 40,
    WARNING: 50,
    ERROR: 60,
}

export enum LogType {
    Debug = 0,
    MobilDebug = 1000,
    Trace = 200000, //100Kb
    Warning = 500000, //250Kb
    Error = 1000000 // 500Kb
}
export interface IMessage {
    message: string
    type: string
    time: string
    caller: string
}

export interface ILogItem {
    /** Log message */
    message: any;
    /** Log level */
    level?: LogLevel;
    /**  */
    error?: unknown;
    /** Inserts the current time in your chosen format before the message */
    timeFormat?: TimeFormat;
    /** For additional info */
    additional?: any;
}
class Singularizer {
    public static trySingularize(name: string, instance: any): boolean {
        if (!this.singularized(name)) {
            (<any>WindowHelper.Instance.Window)[name] = instance;
            return true;
        }
        return false;
    }
    public static singularized(name: string) {
        return (<any>WindowHelper.Instance.Window)[name];
    }
    public static getFreeName(name: string) {
        const win: any = WindowHelper?.Instance.Window ?? {};
        let counter = 0;
        while (win[name + "_" + ++counter]);
        return name + "_" + counter;
    }
}

export class Logger {
    private static logLevel = [LogType.MobilDebug, LogType.Trace, LogType.Debug, LogType.Warning, LogType.Error];
     /**
     * Console log function
     * @param logMessage Message that to be written to the console
     * @param logType console type(Debug, Trace, Warning, Error)
     * @param backgroundColor message background color
     * @param color message text color  
     * @param additional additional info for logging 
     * @param yamlLogType
     * @returns void
     */
        public static Log({ logMessage, logType = LogType.Trace, error, backgroundColor = null, textColor = null, additional = null, source, isClientLog = false, yamlLogType, minEngineLogLevel }:
            { logMessage: any, logType?: LogType, error?: unknown, backgroundColor?: string | null, textColor?: string | null, additional?: any, source?: "model", isClientLog?: boolean, yamlLogType?: LogType, minEngineLogLevel?: LogType }): void {
    
            let colorOptions = backgroundColor || textColor ? "background: " + backgroundColor + "; color: " + textColor : null;
            let targetLog: undefined | ((message: any, ...optionalParams: Array<any>) => void);
            switch (true) {
                case logType == LogType.MobilDebug && this.logLevel.indexOf(LogType.MobilDebug) > -1:
                    targetLog = console.log;
                    break;
                case (logType == LogType.Debug && this.logLevel.indexOf(LogType.Debug) > -1):
                case (logType == LogType.Trace && this.logLevel.indexOf(LogType.Trace) > -1):
                    targetLog = console.log;
                    break;
                case (logType == LogType.Error && this.logLevel.indexOf(LogType.Error) > -1):
                    targetLog = console.error;
                    break;
                case (logType == LogType.Warning && this.logLevel.indexOf(LogType.Warning) > -1):
                    targetLog = console.warn;
                    break;
                case true:
                    break;
            }
    
            let params: [any, ...any[]] = [logMessage];
    
            if (colorOptions && typeof logMessage === "string") {
                params[0] = '%c' + logMessage;
                params.push(colorOptions);
            }
            if (error) {
                params.push(error);
            }
    
            if (additional) {
                params.push(additional);
            }
    
            if (source) { // MUST BE LAST!!!! (used in mobile sim. -> editor logging)
                params.push({ source });
            }
            targetLog?.apply(console, params);
    
            if (isClientLog || (isClientLog && yamlLogType && yamlLogType <= logType) || minEngineLogLevel && minEngineLogLevel <= logType) {
                this.prepareClientLog(logMessage, logType);
            }
        }



     /**
     * Log messages are controlled by their size and add them to the bulk array with type, date and caller function name
     * @param logMessage Message that will be written to the server
     * @param logType Type of log message   
     */
        private static prepareClientLog(logMessage: any, logType: LogType) {
            let currentDateFormat = new Date(Date.now());
            let clientDate = currentDateFormat.getFullYear() + "." + ("0" + (currentDateFormat.getMonth() + 1)).slice(-2) + "." + ("0" + currentDateFormat.getDate()).slice(-2) + " " +
                currentDateFormat.getHours() + ':' + ("0" + currentDateFormat.getMinutes()).slice(-2) + ':' + ("0" + currentDateFormat.getSeconds()).slice(-2) + ':' + currentDateFormat.getMilliseconds();
            ;
            let message: string;
            let whoCallMe = this.getCalleFunctionName();
            if (typeof logMessage !== "string" && logType > 0) {
                try {
                    message = this.shorten(JSON.stringify(logMessage), logType);
                }
                catch (error) {
                    message = "Can not stringfy the given log. Type of log data is: " + Object.prototype.toString.call(logMessage);
                }
            }
            else {
                message = this.shorten(logMessage, logType);
            }
            Logger.LogBulkManager?.writeMessage({ message: message, type: LogType[logType], time: clientDate, caller: whoCallMe });
        }

        /**
     * Can't call Log.calle.caller beacuse of the TypeScript classes are on "strict mode"
     * JS doesn't allow to arguments.callee.caller on strict mode
     * So just throw an simple Error and get the latest function name
     * returns String
     */
        private static getCalleFunctionName(): string {
            // try {
            //     throw new Error();
            // }
            // catch (e) {
            //     try {
            //         return e.stack.split('at ')[4].split(' ')[0];
            //     } catch (e) {
            //         return '';
            //     }
            // }
            return '';
        }

    /**
     * Trim the message with size of the log type
     * @param logMessage log message
     * @param logType Log type that will be controlled
     */
        private static shorten(logMessage: string, logType: LogType) {
            if (logType > logMessage.length) {
                return logMessage
            } else {
                return "Non trimmed message size is: " + logMessage.length;
            }
        };
        private static logBulkManager: IBulkManager | undefined;
        private static get LogBulkManager() {
            if (!Logger.logBulkManager) {
                Logger.logBulkManager = Singularizer.singularized(Logger.windowName);
            }
            return Logger.logBulkManager;
        }
    private static windowName = Singularizer.getFreeName("shrimp_shelldorynemo_pulsar_lbm");
    public static log(logItem: ILogItem) {
        const params = [];

        if (logItem.timeFormat) {
            params.push(this.getDatetimeString(logItem.timeFormat));
        }
        params.push(logItem.message);

        if (logItem.error) {
            params.push(logItem.error);
        }

        if (logItem.additional) {
            params.push(logItem.additional);
        }

        const targetLog = this.getTargetLog(logItem.level);
        targetLog.apply(console, <[any, ...any[]]>params);

    }

    private static getDatetimeString(format: TimeFormat): string {
        const date = new Date();

        switch (format) {
            case "date":
                return date.toLocaleDateString('tr-TR');
            case "datepath":
                return `${date.toISOString().slice(0, 10)}`;
            case "time":
                return `${date.toLocaleTimeString('tr-TR')}.${date.getMilliseconds()}`;
            case "datetime":
                return `${date.toLocaleString('tr-TR')}.${date.getMilliseconds()}`;
        }
    }

    private static getTargetLog(logLevel?: LogLevel): (message: any, ...optionalParams: Array<any>) => void {
        let targetLog: (message: any, ...optionalParams: Array<any>) => void;

        switch (logLevel) {
            case LogLevel.debug:
                targetLog = console.debug;
                break;
            case LogLevel.info:
                targetLog = console.info;
                break;
            case LogLevel.log:
                targetLog = console.log;
                break;
            case LogLevel.trace:
                targetLog = console.trace;
                break;
            case LogLevel.warn:
                targetLog = console.warn;
                break;
            case LogLevel.error:
                targetLog = console.error;
                break;
            default:
                targetLog = console.log;
        }

        return targetLog;
    }
}